<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <li class="with-x" v-if="SearchParams.categoryName">
              {{ SearchParams.categoryName
              }}<i @click="removeCategoryName">×</i>
            </li>
            <li class="with-x" v-if="SearchParams.keyword">
              {{ SearchParams.keyword }}<i @click="removeKeyWord">×</i>
            </li>
            <li class="with-x" v-if="SearchParams.trademark">
              {{ SearchParams.trademark.split(":")[1]
              }}<i @click="removeTrademark">×</i>
            </li>
            <li
              class="with-x"
              v-for="(attr, index) in SearchParams.props"
              :key="index"
            >
              {{ attr.split(":")[1] }}<i @click="removeAttr(index)">×</i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <!-- error:trademarkInfo(trademark) -->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }">
                  <a @click="changCloor('1')"
                    >综合
                    <span
                      v-show="isOne"
                      class="iconfont"
                      :class="{ 'icon-down': isDesc, 'icon-up': isAsc }"
                    ></span>
                  </a>
                </li>
                <li :class="{ active: isTow }">
                  <a @click="changCloor('2')">
                    价格
                    <span
                      v-show="isTow"
                      class="iconfont"
                      :class="{ 'icon-down': isDesc, 'icon-up': isAsc }"
                    ></span>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-list">
            <ul class="yui3-g">
              <li
                class="yui3-u-1-5"
                v-for="(good, index) in goodsList"
                :key="good.id"
              >
                <div class="list-wrap">
                  <div class="p-img" @click="getSkuId(good.id)">
                    <router-link :to="'/detail/' + good.id"
                      ><img v-lazy="good.defaultImg"
                    /></router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i> {{ good.price }}.00</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                      href="item.html"
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                      >{{ good.title }}</a
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- //分页器 -->
          <Pagination
            @getPageNoInfo="getPageNoInfo"
            :pageNo="this.SearchParams.pageNo"
            :pageSize="this.SearchList.totalPages"
            :infoSize="this.SearchList.total"
            :continues="5"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { mapGetters, mapState } from "vuex";
export default {
  name: "Search",
  data() {
    return {
      SearchParams: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        categoryName: "",
        keyword: "",
        order: "1:desc",
        pageNo: 1,
        pageSize: 10,
        props: [],
        trademark: "",
      },
    };
  },
  components: {
    SearchSelector,
  },
  // beforeMount() {
  //   Object.assign(this.SearchParams, this.$route.query, this.$route.params);
  // },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.SearchParams.category1Id = undefined;
      this.SearchParams.category2Id = undefined;
      this.SearchParams.category3Id = undefined;
      Object.assign(this.SearchParams, this.$route.query, this.$route.params);
      this.$store.dispatch("getSearchList", this.SearchParams);
    },
    removeCategoryName() {
      this.SearchParams.categoryName = undefined;
      this.$router.push({
        name: "search",
        params: this.$route.params,
      });
      // watch侦听器会发起getData()
      // this.getData();
    },
    removeKeyWord() {
      this.SearchParams.keyword = undefined;
      this.$bus.$emit("clear");
      this.$router.push({
        name: "search",
        query: this.$route.query,
      });
    },
    trademarkInfo(trademark) {
      this.SearchParams.categoryName = undefined;
      this.SearchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      this.getData();
    },
    removeTrademark() {
      this.SearchParams.trademark = undefined;
      this.getData();
    },
    attrInfo(prop) {
      if (this.SearchParams.props.indexOf(prop) == -1) {
        this.SearchParams.props.push(prop);
        this.getData();
      }
    },
    removeAttr(index) {
      this.SearchParams.props.splice(index, 1);
      this.getData();
    },
    changCloor(flag) {
      // 示例: "1:desc"
      let originOrder = this.SearchParams.order.split(":");
      let originOrderFlag = originOrder[0];
      let OriginOrderSort = originOrder[1];
      //这里要先将 newOrder定义成空字符串 不然newOrder=undefined 然后会把 undefined当成字符串拼接
      let newOrder = "";
      if (flag == originOrderFlag) {
        newOrder = `${flag}:${OriginOrderSort == "desc" ? "asc" : "desc"}`;
      } else {
        newOrder = `${flag}:${"desc"}`;
      }
      this.SearchParams.order = newOrder;
      this.getData();
    },
    getPageNoInfo(pageNo) {
      this.SearchParams.pageNo = pageNo;
      this.getData();
    },
    getSkuId(skuId) {
      this.$bus.$emit("getId", skuId);
    },
  },
  computed: {
    ...mapState({
      SearchList: (state) => state.search.SearchList,
    }),
    ...mapGetters(["goodsList"]),
    isOne() {
      return this.SearchParams.order.indexOf("1") != -1;
    },
    isTow() {
      return this.SearchParams.order.indexOf("2") != -1;
    },
    isDesc() {
      return this.SearchParams.order.indexOf("desc") != -1;
    },
    isAsc() {
      return this.SearchParams.order.indexOf("asc") != -1;
    },
  },
  watch: {
    $route() {
      this.getData();
    },
  },
};
</script>

